* {
  position: relative;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  box-sizing: border-box;
  font-size: 0.8em;
  font-style: normal;
  font-weight: 350;
  font-color: #9098aa;
}

label {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

not(output):-moz-ui-invalid {
  box-shadow: none;
}

.opacity-true {
  opacity: .6;
}

ul, li {
  list-style-type: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
  -webkit-box-shadow: 0 0 0 30px white inset;
}

h1 {
  font-size: 1em;
  margin: 20px 12px;  
}

div { 
  display: flex;
  display: -webkit-flex;
  flex-direction: column;    
}

.input-text label {
  margin-left: auto;
  margin-right: 10px; 
}

input[type=text] {
  width: 330px;
  height: 35px;
  padding: 12px 5px;
  margin: 8px;  
  border: 1px solid #cccccc; 
  background-color: #ffffff;
}

input[type=text]:valid {
  background-color: #ffffff;
}

input:required:valid {
  background-color: #ffffff;
}

input:invalid {
  background-color: #ffffff;
}

.asterisk {
  color: #dc143c;
  font-weight: 700;
}

.radiobuttons {
  margin-left: 20px;
  width: 50%;
}

label input[type="radio"] {
  display: none; 
  text-align: center;
}

label input[type="radio"] ~ span {
  position: relative;
  display: inline-block;
  padding: 3px 0 3px 25px;
  text-align: center;
}

label input[type="radio"] ~ span:before {
  content: "";
  position: absolute;
  display: inline-block;
  width: 18px;
  height: 18px;
  background: #ffffff;
  margin-right: 5px;
  border: 1px solid #cccccc;
  border-radius: 50%;
  left: 0;
  top: 5px;
  box-sizing: border-box;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

label input[type="radio"]:checked ~ span:before {
  border: 5px solid #29d;
}

.ethernet { 
  border: solid 1px #cccccc; 
  grid-area: a;
  height: 570px; 
}

input[type="checkbox"] {
  display: none;   
}

input[type="checkbox"] ~ span {
  position: relative; 
  padding: 3px 0 3px 25px;   
  margin: 15px;
}

input[type="checkbox"] ~ span:before {
  content: "";
  position: absolute;  
  width: 18px;
  height: 18px;
  background: #ffffff;
  margin-right: 5px;
  border: 1px solid #cccccc;  
  left: 0;
  top: 4px;  
  -webkit-transition: all 300ms ease-in-out;  
  transition: all 300ms ease-in-out;
}

input[type="checkbox"]:checked ~ span:before {
  background: #1aa8eb;
}

input[type="checkbox"]:checked ~ span:after {
  content: "";
  position: absolute;
  left: 7px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: 2px solid #ffffff;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

input[type=password] {
  width: 330px;
  height: 35px;
  padding: 12px 5px;
  margin: 8px;  
  border: 1px solid #cccccc; 
  background-color: #ffffff;
}


.message { 
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 1;
  -webkit-animation-name: message;
          animation-name: message;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  z-index: 2;
  font-size: 3em;
  color: #1aa8eb; 
}

.message.search {
  top: -50%;
  font-size: 1.3em;
}

.message.err-submit {
  color: #ff0000;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  font-size: 3em;
}

.message.err-fetch {
  position: absolute;
  top: 160%;
  color: #ff0000;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  font-size: 1em; 
  font-weight: 700;
}

.message.point {
  position: absolute;
  top: 50px;
  left: 83%;
  color: #ff0000;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  font-size: 1.2em; 
}

@-webkit-keyframes message {
  0% {
    opacity: 1;
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 0;
  }
}

@keyframes message {
  0% {
    opacity: 1;
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 0;
  }
}

button.round {  
  position: relative;
  top: 5px;  
  width: 27px;
  height: 27px;
  border: solid 2px #2299dd;
  border-radius: 50%;
  margin-top: 3px;
  margin-right: 30px;
  margin-left: 20px;
  background-color: #ffffff;
  outline: none;   
}

.arrow-round {
  position: relative; 
  left: 3px;
  width: 17px;
  height: 17px;
  border: 2px solid #2299dd;
  border-radius: 50%;
  border-right-color: transparent;
}

.arrow-round::before {
  content: "";
  position: absolute;
  left: 7px;
  top: -3px;
  width: 4px;
  height: 4px;  
  border-right: 2px solid #2299dd;
  border-bottom: 2px solid #2299dd; 
}

button.round:disabled {
  border: solid 2px #cccccc;
}

button.round:disabled *  {
  border: solid 2px #cccccc; 
  border-right-color: transparent; 
}

button.round:disabled .arrow-round::before {
  border-right: 2px solid #cccccc;
  border-bottom: 2px solid #cccccc; 
}

button.round:active {
  opacity: .4;
}

button.round:disabled:active {
  opacity: 1;
}

.dropdown {
  flex-direction: row;
  align-items: center;
}

.select {
  width: 260px;
  height: 30px;
  cursor: pointer;
  border-radius: 2px;
  margin-left: 16px;
}

.select_expand {
  visibility: hidden;
  width: 0;
  height: 30px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -2;
  background-color:transparent;
}

.arrow {
  position: absolute;
  top: 13px;
  right: 85px;
  width: 9px;
  height: 9px; 
  color: #cccccc;
  font-size: 1em;
  pointer-events: none;
  z-index: 3;
  -webkit-animation-duration: .2s;
          animation-duration: .2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  border-right: solid 2px #cccccc;
  border-bottom: solid 2px #cccccc;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.arrow.down { 
    -webkit-animation-name: down-to-up; 
            animation-name: down-to-up; 
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);   
}

.arrow.up {   
    -webkit-animation-name: up-to-down;   
            animation-name: up-to-down;    
}

.select_expandLabel {
  display: block;
  width: 100%;
  height: 30px;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  background-color: transparent;
}

.select_close {
  display: none;
}

.select_closeLabel {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
}

.select_items {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ffffff;
  border-radius: 2px;
  padding-top: 40px;
  border: solid 1px #cccccc;
  z-index: 2;
}

.select_input {
  display: none;
}

.select_label {
  -webkit-transition: all 250ms cubic-bezier(.4, .25, .3, 1);
  transition: all 250ms cubic-bezier(.4, .25, .3, 1);
  display: block;
  height: 0;
  font-size: 0.9rem;
  line-height: 40px;
  overflow: hidden;
  color: #3e3e3e;
  background-color: #ffffff;
  cursor: pointer;
  padding-left: 20px;
}

.select_label-placeholder {
  height: 40px;
  vertical-align: middle;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  background-color: transparent;
  z-index: 4;
}

.select_expand:checked+.select_closeLabel {
  display: block;
  color: transparent;
}

.select_expand:checked+.select_closeLabel+.select_options .select_label {
  height: 40px;
}

.select_expand:checked+.select_closeLabel+.select_options .select_label:hover {
  background-color: transparent;
}

.select_expand:checked+.select_closeLabel+.select_options+.select_expandLabel {
  display: none;
  color: transparent;
}

.select_input:checked+.select_label {
  height: 40px;
  margin-top: -40px;
  color: transparent;
}

input.select_close:disabled~li {
  border: solid 1px #cccccc;
  opacity: .6;
}

.select_items.violet {
  border: solid 1px #9932CC;
}

@-webkit-keyframes down-to-up {
  from {-webkit-transform: rotate(-135deg);transform: rotate(-135deg);}
  to {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
}

@keyframes down-to-up {
  from {-webkit-transform: rotate(-135deg);transform: rotate(-135deg);}
  to {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
}

@-webkit-keyframes up-to-down {
  from {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
  to {-webkit-transform: rotate(-135deg);transform: rotate(-135deg);}
}

@keyframes up-to-down {
  from {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
  to {-webkit-transform: rotate(-135deg);transform: rotate(-135deg);}
}

.wireless-name { 
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end; 
}

.enable-wifi {
  margin-bottom: 20px;  
}

.wireless {
  border: solid 1px #cccccc; 
  grid-area: b;
  height: 570px;
}

.button {
  background-color: #1aa8eb; 
  border: 2px solid #1aa8eb;
  color: #ffffff;
  padding: 2px 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 0.8em;
